  data() {
    return {
      value: 86753.09,
      int: '',
      format: '',
      display: '',
      currency: '',
      // currencies: {
      //   USD: {
      //     symbol: '$',
      //     fraction: 3
      //   }
      // },
      locale: 'en-US'
    }
  },
  mounted() {
    setInterval(() => {
      this.value = chance.integer({ min: 1000, max: 100000 })
    }, 10000)
  },
  methods: {
    change(values: Values) {
      console.log(values)
    }
  }
})
